.features-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 30px;
  column-gap: 30px;
  padding: 0;
  list-style: none;
  margin: 0;

  @include vp-1023 {
    padding-left: 25px;
    column-gap: 0;
  }

  @include vp-767 {
    grid-template-columns: min-content 1fr min-content;
    row-gap: 30px;
    column-gap: 0;
    padding-left: 0;
  }
}

.features-list__item {
  border-left: 5px solid $color-neon-blue;
  padding-top: 40px;
  padding-left: 40px;
  padding-right: 45px;
  fill: $color-neon-blue;
  min-height: 300px;

  @include hover-focus {
    background-color: $color-neon-blue;

    h3,
    p {
      color: $color-font-grey;
    }

    svg {
      fill: $color-font-grey;
    }
  }

  @include vp-1023 {
    min-height: 360px;
  }

  @include vp-767 {
    grid-column-start: 2;
    grid-column-end: 3;
    padding-top: 32px;
    padding-left: 24px;
    padding-right: 30px;
    min-height: 340px;

    &:nth-of-type(3n){
      min-height: 326px;
    }

    &:last-of-type{
      min-height: 326px;
    }
  }
}

.features-list__item svg:first-of-type {
  display: block;

  @include vp-767 {
    display: none;
  }
}

.features-list__item svg:last-of-type {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.features-list h3 {
  margin-top: 24px;
  margin-bottom: 21px;
  font-family: $ff-robotocondensed;
  color: $color-neon-blue;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;

  @include vp-1023 {
    margin-top: 16px;
    margin-bottom: 8px;
    line-height: 34px;
  }

  @include vp-767 {
    font-size: 20px;
  }
}

.features-list__item:last-of-type h3 {
  @include vp-1023 {
    margin-top: 10px;
    margin-bottom: 3px;
  }
}

.features-list p {
  word-wrap: break-word;
  font-family: $ff-ptmono;
  color: $bg-color-dark;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;

  & br {
    display: none;

    @include vp-1023 {
      display: inline;
    }
  }

  @include vp-1023 {
    margin-top: 0;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}
