.about {
  padding-top: 120px;
  padding-bottom: 97px;

  @include vp-1023 {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  @include vp-767 {
    font-size: 50px;
    line-height: 80px;
    padding-top: 44px;
    padding-bottom: 62px;
  }
}

.about img {
  @include vp-767 {
    display: none;
  }
}

.about h2 {
  margin-top: 0;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin-bottom: 11px;

  @include vp-1023 {
    margin-bottom: 0;
  }

  @include vp-767 {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 12px;
  }
}

.about p {
  margin-top: 0;
  font-family: $ff-ptmono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;

  &:last-of-type {
    margin-bottom: 17px;

    @include vp-1023 {
      margin-bottom: 0;
    }

    @include vp-767 {
      margin-bottom: -11px;
    }
  }

  @include vp-1023 {
    margin-top: 0;
    margin-bottom: 0
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}

.about__container {
  display: flex;
  gap: 99px;
  align-items: flex-start;

  @include vp-1023 {
    gap: 30px;
    padding-left: 45px;
  }
}

.moretext-js:first-of-type {
  @include vp-767 {
    display: none;
  }
}

.moretext-js:last-of-type {
  display: none;
}

.about button {
  min-height: 43px;
  min-width: 180px;
  color: $color-neon-blue;
  border-color: $color-neon-blue;

  @include vp-767 {
    min-width: 160px;
    min-height: 30px;
  }
}
