.index-header {
  position: sticky;
  top: 0;
  z-index: 5;
  background: linear-gradient(180deg, #041827 90%, rgba(4, 24, 39, 0) 150%);
  width: 100%;
  min-height: 120px;
  flex-wrap: wrap;
  padding-top: 28px;

  @include vp-1023 {
    position: relative;
    min-height: 100px;
    padding-top: 19px;
  }

  @include vp-767 {
    & {
      min-height: 70px;
    }
  }
}

.index-header svg {
  fill: $color-neon-blue;
}

.index-header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-font-grey;

  @include vp-1023 {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.index-header__logo-wrapper {
  @include vp-1023 {
    margin-right: auto;
  }

  @include vp-767 {
    & p {
      display: none;
    }
  }
}

.index-header__logo-wrapper svg:first-of-type {
  display: block;

  @include vp-1023 {
    display: block;
  }

  @include vp-767 {
    display: none;
  }
}

.index-header__logo-wrapper svg:last-of-type {
  display: none;

  @include vp-1023 {
    display: none;
  }

  @include vp-767 {
    display: block;
  }
}

.index-header__constacts-wrapper {
  display: inline-flex;
  flex-wrap: wrap;
  margin-right: auto;
  margin-left: auto;
  column-gap: 91px;

  & p {
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
  }

  @include vp-1023 {
    gap: 6px;
    margin-right: 12px;

    & p {
      margin-top: 0;
    }
  }

  @include vp-767 {
    margin-right: 3px;
  }

  & a {
    color: $color-font-grey;
    text-decoration: none;
    padding: 18px;

    @include hover-focus {
      & svg {
        fill: $color-font-grey;
      }
    }
  }

  & svg {
    vertical-align: middle;
  }
}

.index-header__constacts-wrapper span {
  word-break: keep-all;

  @include vp-1023 {
    display: none;
  }
}

.index-header button {
  margin-right: 60px;
  cursor: pointer;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  min-width: 160px;
  min-height: 40px;
  align-self: center;
  display: inline-block;
  vertical-align: middle;
  padding: 10px 15px;
  border: 1px solid $color-font-grey;
  border-radius: 3px;
  background: transparent;
  color: $color-font-grey;
  text-transform: uppercase;
  margin-bottom: 1px;

  @include vp-1023 {
    display: none;
  }

  @include hover-focus {
    background: $color-default-white;
    color: $color-default-black;
  }
}
