.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  & form {
    & p:first-of-type {
      margin-bottom: 15px;
    }

    & label {
      margin-left: 10px;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
    }
  }

  & [type="text"],
  & [type="tel"],
  & textarea {
    width: 98%;
    background: #019bdf;
    border: 0;
    padding: 15px;
    border-radius: 3px;
    color: $color-default-white;
    font-family: $ff-ptmono;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 20px;
  }

  & [type="tel"] {
    margin-bottom: 0;
  }

  & textarea {
    min-height: 155px;
    margin-bottom: 28px;
    padding-top: 26px;
  }

  & h3 {
    font-family: $ff-robotocondensed;
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
    color: $color-default-white;
    margin-bottom: 0;
    margin-top: 0;
  }

  & p {
    font-family: $ff-ptmono;
    color: $color-default-white;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 37px;
  }

  & [type="submit"] {
    width: 98%;
    margin-bottom: 15px;
    background-color: $bg-color-dark;
    border: none;
    min-height: 45px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.05em;
  }


  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;
    height: 610px;
    border-radius: 3px;
    width: 500px;
    padding: 40px;
    padding-left: 32px;
    background-color: $color-neon-blue;

    & p {
      margin-top: 0;
    }

    @include vp-767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;
    fill: $color-default-white;
    background: $color-transparent;
    border: none;
    cursor: pointer;

    @include hover-focus {
      fill: $color-font-black;
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}
