.social__list {
  display: flex;
  gap: 15px;
  list-style: none;
  padding: 0;
  margin: 0;
  padding-left: 10px;
  flex-wrap: wrap;

  & a {
    display: inline-block;
    vertical-align: middle;
    padding: 5px;
    padding-bottom: 0;
  }

  & svg {
    fill: #f8fdff;

    @include hover-focus {
      fill: #f8fdffa7;
    }
  }

  @include vp-1023 {
    margin-right: 20px;
    padding: 0;
  }

  @include vp-767 {
    margin-right: 0;
  }
}
